.messageBox {
   background: #f3f3f3;
   border-radius: 20px;
   padding: 5px 10px;
   color: white;
   display: inline-block;
   max-width: 80%;
}

.messageText {
   width: 100%;
   letter-spacing: 0.8px;
   float: left;
   font-size: 1em;
   word-wrap: break-word;
   margin: 5px;
}

.messageText img {
   vertical-align: middle;
}

.messageContainer {
   display: flex;
   justify-content: flex-end;
   padding: 0 5%;
   margin-top: 3px;
}

.sentText {
   display: flex;
   align-items: center;
   font-family: Helvetica;
   color: #828282;
   letter-spacing: 0.3px;
}

.pl-10 {
   padding-left: 10px;
}

.pr-10 {
   padding-right: 10px;
}

.justifyStart {
   justify-content: flex-start;
}

.justifyEnd {
   justify-content: flex-end;
}

.colorWhite {
   color: white;
}

.colorDark {
   color: black;
}

.backgroundBlue {
   background: #4c4c6d;
}

.backgroundLight {
   background: #afb9c8;
}
